<template>
    <div class="nav-wrapper">
        <nav class="navbar">
            <img src="@/assets/images/logo.svg" alt="company website logo" class="navbar__logo">
            <!-- Desktop nav -->
            <ul class="desktop-menu">
                <li class="desktop-menu__item" v-for="link in links" :key="link.name">
                    <a :href="link.link" class="desktop-menu__link">{{ link.name }}</a>
                </li>
            </ul>
            <gradientButton class="btn">Request Invite</gradientButton>
            <!-- Mobile Nav -->
            <div class="hamburger" :class="{ 'hamburger--isOpen': mobileOpen }" @click="mobileOpen = !mobileOpen">
                <span class="hamburger__line"></span>
                <span class="hamburger__line"></span>
                <span class="hamburger__line"></span>
            </div>
        </nav>
        <!-- Mobile nav menu -->
        <transition name="fade">
            <div class="mobile-menu" v-show="mobileOpen" :key="1">
                <transition name="fade">
                    <ul class="menu" :class="{ 'menu--isOpen': mobileOpen }" v-show="mobileOpen">
                        <li class="menu__item" v-for="link in links" :key="link.name">
                            <a class="menu__link" :href="link.link">{{ link.name }}</a>
                        </li>
                    </ul>
                </transition>
            </div>
        </transition>
    </div>
</template>

<script>
    import gradientButton from '@/components/GradientButton'

    export default {
        components: {
            gradientButton
        },
        data() {
            return {
                mobileOpen: false,
                links: [
                    { name: 'Home', link: '#'},
                    { name: 'About', link: '#'},
                    { name: 'Contact', link: '#'},
                    { name: 'Blog', link: '#'},
                    { name: 'Careers', link: '#'}
                ]
            }
        }
    }
</script>

<style lang="scss" scoped>
@import '@/assets/scss/style.scss';

.nav-wrapper {
    width: 100%;
    background: $white;
    position: fixed;
    z-index: 999;
}

.navbar {
    max-width: 1200px;
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 2rem 2.5rem;
    margin-inline: auto;
}

.hamburger {
    @include lg {
        display: none;
    }
    &--isOpen {
        :nth-child(1) {
            transform: translateY(7px) rotate(135deg);
        }
        :nth-child(2) {
            opacity: 0;
        }
        :nth-child(3) {
            transform: translateY(-7px) rotate(-135deg);
        }
    }
    &__line {
    display: block;
    width: 3rem;
    height: .2rem;
    margin-bottom: .5rem;
    background: $dark-blue;
    opacity: 1;
    transition: all .35s ease;
    }
}

.mobile-menu {
    position: fixed;
    width: 100%;
    top: 6.1rem;
    padding: 2rem;
    background-image: linear-gradient(hsl(0,0,0,.75), hsl(0,0,0,.1));
    min-height: 100vh;
    z-index: 999;
}

.menu {
    position: relative;
    list-style: none;
    display: flex;
    flex-direction: column;
    align-items: center;
    background: $white;
    padding: 3rem;
    border-radius: .5rem;
    gap: 2rem;
    &__link {
        font-size: 1.8rem;
        text-decoration: none;
        color: inherit;
    }
}

.desktop-menu {
    display: none;
    gap: 3rem;
    @include lg {
        display: flex;
    }
    &__item {
        
    }
    &__link {
        display: flex;
        position: relative;
        text-decoration: none;
        color: $gray-blue;
        font-size: 1.4rem;
        cursor: pointer;
        &::after {
            content: "";
            position: absolute;
            top: 4.7rem;
            width: 0%;
            height: .4rem;
            background-image: linear-gradient($cyan, $lime-green);
            transition: width .25s ease;
        }
        &:hover::after {
            width: 100%;
        } 
        
    }
}

.btn {
    display: none;
    @include lg {
        display: block;
    }
}

.fade-enter {
    opacity: 0;
}

.fade-leave-to {
    opacity: 0;
}

.fade-enter-active,
.fade-leave-active {
    transition: opacity .5s ease;
}
</style>